.profile {
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
}

.description {
  padding: 30px 0;
  text-align: center;
  background-color: var(--white);
  border-radius: 5px 5px 0 0;
}

.avatar {
  width: 100px;
  border-radius: 50%;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
}

.name {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 10px;
}

.tag,
.location {
  color: var(--gray);
  margin-bottom: 10px;
}
.stats {
  display: flex;
  background-color: var(--light-gray);
  border-radius: 0 0 5px 5px;
}
.item {
  width: calc(100% / 3);
  text-align: center;
  border: var(--gray) 1px solid;
  padding: 20px 5px;
}

.label {
  display: block;
  color: var(--gray);
  margin-bottom: 8px;
}

.quantity {
  font-weight: 600;
}
